    gap: 40px;
    display: flex;
  }

  .top-bar__nav_list_link-item {
    cursor: pointer;
    color: #344f10;
    font-size: 18px;
    font-weight: 500;
    line-height: 27px;
    text-decoration: none;
  }

  .top-bar__nav_list_link-item:after {
    content: "";
    background-color: #333;
    width: 100%;
    height: 1px;
    transition: transform .3s;
    display: block;
    bottom: -8px;
    transform: scale(0);
  }

  .top-bar__nav_list_link-item:hover:after {
    transform: scale(1);
  }

  .top-bar__nav_list_link-item:hover {
    color: #8c9772;
  }
}

